@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.full-vh {
  height: 100vh;
}

@layer utilities {
  /* sm-md */
  .artwork-frame-sm-md {
    /* 1.78 = image ratio */
    height: calc((1 / 1.78) * 100vw + 100px - 0.75rem * 2);
  }

  /* lg */
  .artwork-frame-lg {
    height: calc(100vh - 0.75rem * 2);
    /* height: 100vh; */
    width: calc(50vw - 0.75rem * 2);
  }
}

@font-face {
  font-family: "benaco-one";
  src: local("benaco-one"),
    url(./fonts/benaco-one-regular.otf) format("opentype");
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

@font-face {
  font-family: "ibm-plex-sans-thai-looped";
  src: local("ibm-plex-sans-thai-looped"),
    url(./fonts/ibm-plex-sans-thai-looped.ttf) format("truetype");
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

@font-face {
  font-family: "gt-super-text-bold";
  src: local("gt-super-text-bold"),
    url(./fonts/gt-super-text-bold.otf) format("opentype");
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}
